    //export default function tagPageView (url, title) {
        export const tagPageView = (url, title) => {
        window.dataLayer.push({
            event: 'pageview',
            page: {
              url: url,
              title: title
            }
        })
    }

    //export default function tagEvent (category, action, label) {
    export const tagEvent = (category, action, label) => {
        window.dataLayer.push({
            event: 'event', 
            eventProps: {
                category: category,
                action: action, 
                label: label
            }
        })
    }
//}

